export default {
  ShowFoot: ({commit}) => {
    commit('ShowFooter')
  },
  HideFoot: ({commit}) => {
    commit('HideFooter')
  },
  MGMenuBodyToggleBtn: ({commit}) => {
    commit('MGMenuBodyToggle')
  },
  LoginToggleBtn: ({commit}) => {
    commit('LoginToggle')
  }
}



