    width: 30.5%;
}
.playlist .item .item_info {
    float: left;
    width: 69.5%;
    margin: 12px 0;
    text-align: left;
    padding-left: 15px;
}
.playlist .item .item_info h3 {
    font-size: 0.8em;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: normal;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    display: -webkit-box;
}
.playlist .item .item_info .nowplaying {
    color: #fff;
    text-transform: uppercase;
    background-color: #3B98F0;
    border-radius: 3px;
    font-size: 0.75em;
    text-align: center;
    width: 110px;
    margin-top: 3px;
    padding: 2px 0;
}
.playlist .item .item_info h4 {
    font-size: 0.75em;
    line-height: 1.4em;
    margin-top: 3px;
    display: block;
    color: #8D8E8F;
}

.sg_video_wrap {
    width: 100%;
    overflow: hidden;
    position: relative;
    margin-top: 50px;
    padding-top: 50px;
}
.sg_video_wrap .title {
    color: rgba(0,0,0,0.9);
    font-size: 24.8px;
    font-weight: bold;
    text-align: center;
    letter-spacing:-0.248px;
    padding-left: 20px;
}
.sg_video_wrap .video_list {
    width: 100%;
    zoom: 1;
    transition: transform 200ms ease-in-out;
}
.sg_video_wrap .video_list.left {
    transform: translateX(0px);
}
.sg_video_wrap .video_list.right {
    transform: translateX(-50%);
}
.sg_video_wrap .video_list::after {
    content: "";
    display: block;
    clear: both;
}
.sg_video_wrap li {
    float: left;
    zoom: 1;
    width: 100%;
}
.sg_video_wrap li::after {
    content: "";
    display: block;
    clear: both;
}
.sg_video_wrap .video{
    float: left;
    width: 50%;
    border-radius: 3px;
    padding: 20px 20px;
}
.sg_video_wrap a{
    width: 100%;
}
.sg_video_wrap .img_wrap{
    position: relative;
    transition: box-shadow 100ms;
    width: 100%;
    padding-top: 56.25%;
    background: #ddd;
    transition:  box-shadow 200ms ease;
}
.sg_video_wrap .video:hover .img_wrap{
    box-shadow: 0 0px 10px 10px rgba(0,0,0,0.75);
}
.sg_video_wrap img{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    transition: transform 200ms ease;
    transform: scale(1);
    line-height: 200px;
}
.sg_video_wrap .video:hover img{
    transform: scale(1.03);
}
.sg_video_wrap .video_btn {
    position: absolute;
    top: 50%;
    transform: translateY(-.5);
    width: 48px;
    height: 60px;
    opacity: .4;
    border:none;
    outline: none;
    border-radius: 3px;
    background-color: rgba(241, 234, 234, 0.7);
    background-position: center;
    background-size: 34px 62px;
}
.sg_video_wrap .video_left {
    left: 30px;
    background-image: url(../../assets/images/arrow-next.png);
}
.sg_video_wrap .video_right {
    right: 30px;
    background-image: url(../../assets/images/arrow-prev.png);
}

@media screen and (max-width:1024px){
    .video_watch_container {
        width: 100%;
    }
    .playlist {
        width: 100%;
        z-index: 10001;
    }
    .playlist .item .audio_pause {
        height: 100%;
    }
    .playlist .item .audio_pause::before {
        transform: translate(-11px,-12%);
        top: 50%;
    }
    .playlist .item .audio_pause::after {
        transform: translate(-3px,50%);
        top: 50%;
    }
}

</style>


